@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Teko:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Teko:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #CAE7B9;
  font-family: 'Teko', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.MuiAppBar-colorPrimary {
    background-color: #40798C !important;
}

.app-bar-container {
    padding: 10px;
    font-size: 2em;
}

.landing-root-container {
    color: #1F363D;
}

.landing-root-container .header-container {
    text-align: center;
    font-size: 4em;
    font-weight: bold;
    margin-top: 50px;
    font-family: 'Lobster', cursive;
}

.landing-root-container .subheader-container {
    text-align: center;
    font-size: 1.75em;
    font-weight: bold;
}
.search-bar-container {

}

.search-bar-container .search-container {
    text-align: center;
    margin-top: 50px;
}
